import React, { useEffect, useState } from 'react';
import { EditEntry, DeleteDialog } from '../../components';
import { timeConvert } from '../../auxFunctions';

import './SearchCard.css';

export default function SearchCard(props) {
    const { data, index, setHover, logged, flip, setFlip, setData, full } = props

    const updateHoverGood = () =>{
        setHover(index)
    }

    const updateHoverNull = () => {
        setHover(-1)
    }

    const [open, setOpen] = useState(false)
    const editEntry = () => {
      setOpen(true)
    }
    const editEntryClose = () => setOpen(false)

    const [open1, setOpen1] = useState(false)
    const deleteEntry = () =>{
      setOpen1(true)
    }
    const deleteEntryClose = () => setOpen1(false)

    return(
        <div className='searchCard' onMouseEnter={updateHoverGood} onMouseLeave={updateHoverNull}>
            <span className='carTitle'>{data.name}</span>
            <span>{data.desc}</span>
            <span>{data.address}</span>
            <div className='carddiv'></div>
            <div className='cardflex'>
                <span className='cardTitle'>Hours: </span>
                <div style={{width: 5}}></div>
                <span>{ data.hours.length == 2 ? `${timeConvert(data.hours[0])} - ${timeConvert(data.hours[1])}` : null}</span>
            </div>
            <div className='cardflex'>
                <span className='cardTitle'>Cost: </span>
                <div style={{width: 5}}></div>
                <span>
                    {data.cost === 'low' ? '$ (Low)' : ''}
                    {data.cost === 'mid' ? '$$ (Mid)' : ''}
                    {data.cost === 'high' ? '$$$ (High)' : ''}
                </span>
            </div>
            <div className='cardflex'>
                <span className='cardTitle'>Website: </span>
                <div style={{width: 5}}></div>
                <span style={{color: 'blue'}}>
                    <a href={data.link} target='_blank'>Take me to site</a>
                </span>
            </div>
            { logged ? (
                <>
                    <div className='topright'>
                        <div className='butright' onClick={editEntry}>Edit</div>
                        <div className='butright' onClick={deleteEntry}>Delete</div>
                    </div>
                    <EditEntry
                        open={open}
                        onClose={editEntryClose}
                        flip={flip}
                        setFlip={setFlip}
                        data={data}
                        index={index}
                        setData={setData}
                        full={full}
                    />
                    <DeleteDialog
                        open={open1}
                        onClose={deleteEntryClose}
                        flip={flip}
                        setFlip={setFlip}
                        data={data}
                        index={index}
                        setData={setData}
                        full={full}
                    />
                </> 
            ): null }
        </div>
    )
} 

