export default function formalTimeConvert (timeStr) {
    let timeList = timeStr.split(':')
    let hour = timeList[0]
    let minutes = timeList[1]
    if (parseInt(minutes) < 10){
        minutes = "0" + parseInt(minutes).toString()
    }

    hour = (parseInt(hour)).toString()

    if (hour.length == 1){
        hour = "0"+ hour
    }

    return `${hour}:${minutes}`
}
