export default function timeCheck (stime, etime) {
    let stimeList = stime.split(':')
    let shour = parseInt(stimeList[0])
    let sminutes = parseInt(stimeList[1])

    let etimeList = etime.split(':')
    let ehour = parseInt(etimeList[0])
    let eminutes = parseInt(etimeList[1])

    if (ehour > shour){
        return true
    } else if (shour === ehour){
        if (sminutes < eminutes){
            return true
        }else{
            return false
        }
    }else{
        return false
    }
}
