import React, { useEffect, useState } from 'react';
import Radio from '@material-ui/core/Radio';

import './VidFilter.css';

export default function VidFilter(props) {
  let { toggle, setDuration, setCats, setDiff} = props

  useEffect(()=>{
    setCats(['M','E'])
  },[])

  const [low, setLow] = useState(false)
  const togLow = () => setLow(!low)
  const [mid, setMid] = useState(false)
  const togMid = () => setMid(!mid)
  const [high, setHigh] = useState(false) 
  const togHigh = () => setHigh(!high)

  useEffect(() =>{
    let newL = []
    if (low) newL.push('low')
    if (mid) newL.push('mid')
    if (high) newL.push('high')
    setDuration(newL)
  },[low, high, mid])

  const [music, setMusic] = useState(false);
  const togMusic = () => setMusic(!music);
  const [exc, setExc] = useState(false);
  const togExc = () => setExc(!exc);

  useEffect(()=>{
    let catList = []
    if (music){
      catList.push('M')
    }
    if (exc){
      catList.push('E')
    }
    setCats(catList)
    if (catList.length === 0){
      setCats(['M','E'])
    }
  },[music, exc])

  const [lowD, setLowD] = useState(false)
  const togLowD = () => setLowD(!lowD)
  const [midD, setMidD] = useState(false)
  const togMidD = () => setMidD(!midD)
  const [highD, setHighD] = useState(false)
  const togHighD = () => setHighD(!highD)
  useEffect(() =>{
    let newL = []
    if (lowD) newL.push('1')
    if (midD) newL.push('2')
    if (highD) newL.push('3')
    setDiff(newL)
  },[lowD, highD, midD])

  return (
    <div className={`filterContainer lower_vidf ${toggle ? '' : 'closeFil' }`}>
      <div>
        <div className='filminititle'>Difficulty</div>
        <div className='costBar extend_vidf'>
          <div className={`dollarhov ${lowD ? 'hardenCost' : ''}`} name='low' onClick={togLowD}>1</div>
          <span className={`dollarhov ${midD ? 'hardenCost' : ''}`} name='mid' onClick={togMidD}>2</span>
          <span className={`dollarhov ${highD ? 'hardenCost' : ''}`} name='high' onClick={togHighD}>3</span>
        </div>
        <div className='filminititle'>Duration</div>
        <div className='costBar extend_vidf'>
          <div className={`dollarhov ${low ? 'hardenCost' : ''}`} name='low' onClick={togLow}>Short</div>
          <span className={`dollarhov ${mid ? 'hardenCost' : ''}`} name='mid' onClick={togMid}>Medium</span>
          <span className={`dollarhov ${high ? 'hardenCost' : ''}`} name='high' onClick={togHigh}>Long</span>
        </div>
        <div className='filminititle'>Resource type</div>
        <div className='catBox'>
          <span style={{ marginLeft: '3px'}}>No selections will show all</span>
          <div className='divBar'/>
          <div className='catSplit'>
            <div className='catCol catleft'>
              <div className='entriesCat'>
                <Radio checked={music} onClick={togMusic} color='primary' size='small'/>
                <span>Music</span>
              </div>
              <div className='entriesCat'>
                <Radio checked={exc} onClick={togExc} color='primary' size='small'/>
                <span>Excercise</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
} 