import React, { useEffect, useState } from 'react';
import { EditVideo, DeleteVideo } from '../../components';
import Axios from 'axios';

import './VideoCard.css';

export default function VideoCard(props) {
    const { id, videoID, cate, duration, name, diff, index, fullData, flip, setFlip, logged, setFullData } = props

    const [cat, setCat] = useState(cate)
    const [render, setRender] = useState(false)
    const [tmb, setTmb] = useState('')

    const [open, setOpen] = useState(false)
    const editEntry = () => {
      setOpen(true)
    }
    const editEntryClose = () => setOpen(false)

    const [open1, setOpen1] = useState(false)
    const deleteEntry = () =>{
      setOpen1(true)
    }
    const deleteEntryClose = () => setOpen1(false)

    useEffect(()=>{
        grabTmb()
    },[videoID])

    const {REACT_APP_GOOGLEAPI} = process.env

    const grabTmb = async () => {
        let answer;
        try{
            answer = await Axios.get(`https://youtube.googleapis.com/youtube/v3/videos?part=snippet&part=contentDetails&id=${videoID}&key=${REACT_APP_GOOGLEAPI}`)
        } catch(err){
            console.log(err)
        } 
        setTmb(answer.data.items[0].snippet.thumbnails.high.url)
        setRender(true)
    }

    const taketoSite = () => {
        let url = `https://www.youtube.com/watch?v=${videoID}`
        window.open(url, '_blank');
    }

    return(
        <div className='vidcardcont_vid'>
            <div style={{margin: 'auto'}}> 
                { render ? (
                    <>
                        { logged ? (
                                <>
                                    <div className='vidbutton_vid'>
                                        <div className='butright' onClick={editEntry}>Edit</div>
                                        <div className='butright' onClick={deleteEntry}>Delete</div>
                                    </div>
                                    <EditVideo
                                        myId={id}
                                        open={open}
                                        onClose={editEntryClose}
                                        flip={flip}
                                        setFlip={setFlip}
                                        index={index}
                                        videoID={videoID}
                                        cat={cate}
                                        diff={diff}
                                        full={fullData}
                                        setFull={setFullData}
                                    />
                                    <DeleteVideo
                                        myId={id}
                                        open={open1}
                                        onClose={deleteEntryClose}
                                        flip={flip}
                                        name={name}
                                        setFlip={setFlip}
                                        index={index}
                                        cat={cate}
                                        videoID={videoID}
                                        diff={diff}
                                        duration={duration}
                                        full={fullData}
                                        setFull={setFullData}
                                    />
                                </>
                            ) : null }
                        <div className='t_v' onClick={taketoSite}>
                            <span className='vidName_vid '>{name}</span>
                            {/* <div className='bar_vid'></div> */}
                            <div style={{height: '5px'}}></div>
                            <span className='subtext_vid'>Difficulty: {diff}</span>
                            <br></br>
                            <span className='subtext_vid'>Time: {duration} mins</span>
                        </div>
                        <div className='padtop_vidTmb' onClick={taketoSite}>
                            <img className='vidTmbContain' src={tmb}></img>
                        </div>
                    </>
                ): null}
            </div>
        </div>
    )
} 

