import React, { useState, useEffect } from 'react';
import { FiMapPin } from 'react-icons/fi';

import './Marker.css';

// Basic marker component --> Using Icon from React Icons
export default function MyMarker(props) {

    // this State is the toggle for whether the marker will be "highlighted"
    const [isHover, setIsHover] = useState(false)

    // If we detect an update to the hover, then we update all componenents
    useEffect(()=>{
        if (props.hover === props.index){
            setIsHover(true)
        } else{
            setIsHover(false)
        }
    },[props.hover])

    return(
        <div>
            <div className={`markContainer ${isHover ? 'hovering' : '' }`}>
            <FiMapPin className="pin"/>
            </div>
            <div className="markerText">
                <span className={`${isHover ? 'showText': ''}`}>{props.text}</span>
            </div>
        </div>
    );
}