export default function timeConvert (timeStr) {
    let timeList = timeStr.split(':')
    let hour = timeList[0]
    let minutes = timeList[1]
    if (parseInt(minutes) < 10){
        minutes = "0" + parseInt(minutes).toString()
    }

    var timeStamp
    if (parseInt(hour) > 12){
        hour = (parseInt(hour)-12).toString()
        timeStamp = "P.M."
    }else{
        hour = (parseInt(hour)).toString()
        timeStamp = "A.M."
    }

    return `${hour}:${minutes} ${timeStamp}`
}
