import React, { useEffect, useState } from 'react';
import Axios from 'axios';
import Dialog from '@material-ui/core/Dialog';

import './DeleteVideo.css';

export default function DeleteVideo(props) {
    let { myId, onClose, open, flip, setFlip, name, videoID, diff, duration, index, full, setFull } = props

    const deleteHandle = async () => {
        var request = null
        try{
            request = await Axios.post(`/video/deleteVideo`, 
                {
                    id: myId
                }
            )
        } catch(err){
            console.log(err)
        }

        let info = full
        info.splice(index, 1)
        setFull(info)
        setFlip(!flip)
        onClose()
    }

    return(
        <div>
            <Dialog open={open} onClose={onClose}>
                <div className='DDbox'>
                    <div>
                        <span>Are you sure you want to delete video entry: {name}?</span>
                    </div>
                    <div className='DDbuttonbox'>
                        <button onClick={deleteHandle} className='DDbutton DDleft'>Delete</button>
                        <button onClick={onClose} className='DDbutton DDright'>Cancel</button>
                    </div>
                </div>
            </Dialog>
        </div>
    )
}