import React, { useEffect, useState } from 'react';
import SearchIcon from '@material-ui/icons/Search';
import MenuOpenIcon from '@material-ui/icons/MenuOpen';
import { VidFilter, VideoCard, NewVideo } from '../../components';
import Axios from 'axios';

import './VidPage.css';

import Banner from '../../statics/videoBanner.png';

export default function VidPage(props) {
    const [search, setSearch] = useState('')
    const updateSearch = event => setSearch(event.target.value)

    const [load, setLoad] = useState(false)
    const [show, setShow] = useState(false)

    const [filter, toggleFilter] = useState(false)
    const setFilter = () => toggleFilter(!filter)

    const [logged, setLogged] = useState(localStorage.getItem('jwt'))

    const [vidList, setVidList] = useState([])

    const [flip, setFlip] = useState(false)
    
    // filters
    const [duration, setDuration] = useState([]) 
    const [diff, setDiff] = useState([])
    const [cats, setCats] = useState(['M','E'])
    
    const handleKeyPress = (event) => {
        if(event.key === 'Enter'){
            searchData()
        }
    }

    const findResults = async () =>{
        
        if (search === ''){
            return alert("Please enter something into search bar.")
        } 

        setLoad(true)
        setShow(false)

        let myReq = {
            search,
            filters: {
                duration,
                category: cats,
                diff,
            }   
        }

        var answer = null
        try{
          answer = await Axios.post('/video/allVideos', myReq)
        } catch(err){
          console.log(err)
        } 

        answer = answer.data

        setVidList(answer)
        setLoad(false)
        setShow(true)
    }

    const searchData = () => {
        findResults()
    }

    const [open, setOpen] = useState(false)
    const newEntry = () => {
    setOpen(true)
    }
    const newEntryClose = () => setOpen(false)

    

    return (
        <div className='vidContainer_vidc'>
            <div className="topcolor_vidp">
                <div className='barBox'>
                    <div className='seudoBar'>
                        <input type='text' placeholder='Search video resources' className='searchBar' onKeyPress={handleKeyPress} onChange={updateSearch} value={search}></input>
                        <div className='searchIcon button' onClick={searchData}>
                            <SearchIcon />
                        </div>
                    </div>
                    <div className={`filterButton button ${filter ? 'moveFilter' : ''}`} onClick={setFilter}>
                        <div className={`menuIcon ${filter ? 'reflect' : ''}`}>
                            <MenuOpenIcon/>
                        </div>
                        <span>Filters</span>
                    </div>
                </div>
                <div className='fil'>
                    <VidFilter toggle={filter} setDuration={setDuration} setDiff={setDiff} setCats={setCats}/>
                </div>
            </div>
            { logged ? (
                <>
                    <div style={{textAlign:'center', width:'100%'}}>
                        <div className='addVideo' onClick={newEntry}>Add New Video Entry</div>
                    </div> 
                    <NewVideo 
                        open={open}
                        onClose={newEntryClose}
                        flip={flip}
                        setFlip={setFlip}
                        full={vidList}
                        setFull={setVidList}
                    />  
                </>
            ) : null }
            <div className='whiteback'>
                { show ? (
                    <>
                        <div className='vidDisp_vid'>
                            {(vidList.length !== 0) ? (
                                <>
                                    {
                                        vidList.map((object, index) => <VideoCard 
                                            id={object._id}
                                            videoID={object.videoID}
                                            duration={object.duration}
                                            name={object.name}
                                            diff={object.diff}
                                            cate={object.category}
                                            index={index}
                                            fullData={vidList}
                                            setFullData={setVidList}
                                            flip={flip}
                                            setFlip={setFlip}
                                            logged={logged}
                                            key={index}
                                        />)
                                    }
                                </>
                            ) : null }
                        </div>
                    </>
                ) : load ? (
                    <>
                        <div className='midLoad martop_vidc' style={{color: 'black', backgroundColor: 'white'}}>
                            Loading results...
                        </div>
                    </>
                ) : (
                    <>
                        <div className='searchintro vidUse' style={{color: 'black', backgroundColor: 'white', overflowY: 'hidden'}}>
                            <img className='support' src={Banner}></img>
                        </div>
                    </>
                ) }
            </div>
        </div>
    )
} 

