import React, { useEffect, useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import Axios from 'axios';

import { CategorySelect } from '../../components';
import { formalTimeConvert, timeCheck } from '../../auxFunctions';

import './EditEntry.css'

export default function EditEntry(props) {
    let { onClose, open, flip, setFlip, data, index, setData, full } = props

    useEffect(()=>{
        setName(data.name)
        setZip(data.zip)
        setAddress(data.address)
        setCity(data.city)
        setCost(data.cost)
        setLink(data.link)
        setDesc(data.desc)
        setCat(data.category)
        setstime(data.hours[0])
        setetime(data.hours[1])
    },[data])
  
    const [name, setName] = useState(data.name)
    const updateName = (event) => setName(event.target.value)
    const [address, setAddress] = useState(data.address)
    const updateAddress = (event) => setAddress(event.target.value)
    const [zip, setZip] = useState(data.zip)
    const updateZip = (event) => setZip(event.target.value)
    const [city, setCity] = useState(data.city)
    const updateCity = (event) => setCity(event.target.value)
    const [cost, setCost] = useState(data.cost)
    const updateCost = (event) => setCost(event.target.value)
    const [link, setLink] = useState(data.link)
    const updateLink = (event) => setLink(event.target.value)
    const [desc, setDesc] = useState(data.desc)
    const updateDesc = (event) => setDesc(event.target.value)
    const [cat, setCat] = useState(data.category)

    const [stime, setstime] = useState(data.hours[0])
    const updatestime = (event) => setstime(event.target.value)
    const [etime, setetime] = useState(data.hours[1])
    const updateetime = (event) => setetime(event.target.value)

    const closeUp = () => {
        setName(data.name)
        setZip(data.zip)
        setAddress(data.address)
        setCity(data.city)
        setCost(data.cost)
        setLink(data.link)
        setDesc(data.desc)
        setCat(data.category)
        setstime(data.hours[0])
        setetime(data.hours[1])
        onClose()
    }

    const handleAdd = async () =>{
        if (name.length === 0 || address.length === 0 || zip.length === 0 || city.length === 0 || link.length === 0 || desc.length === 0 || !/^\d+$/.test(zip) ){
            return window.alert('Make sure fields are filled and zip is a number.')
        }

        if (timeCheck(stime, etime) == false){
            return window.alert('Make sure hours are ordered and valid')
        }

        let newdata = {
            id: data.id,
            name,
            address,
            zip,
            city,
            cost,
            link,
            desc,
            category: cat,
            hours: [stime, etime]
        }

        var request = null
        try{
            request = await Axios.post('/api/updateStore', newdata)
        } catch(err){
            console.log(err)
        }

        let info = full
        info[index] = newdata
        setData(info)
        setFlip(!flip)
        closeUp()
    } 
    
    return(
        <div>
            <Dialog onClose={closeUp} open={open}>
                <div className='ADbox'>
                    <div className='ADinput ADtext'>
                        <span>Update Entry</span>
                    </div>
                    <div className='ADinput'>
                        <TextField size='small' label="Name" variant="outlined" value={name} onChange={updateName} style={{ width: '100%' }}/>   
                    </div>
                    <div className='ADinput'>
                        <TextField size='small' label="Address" variant="outlined" value={address} onChange={updateAddress} style={{ width: '100%' }}/>   
                    </div>
                    <div className='ADinput ADsplit'>
                        <div>
                            <TextField size='small' label="City" variant="outlined" value={city} onChange={updateCity} style={{ width: '100%' }}/>  
                        </div>
                        <div>
                            <TextField size='small' label="Zip" variant="outlined" value={zip} onChange={updateZip} style={{ width: '100%' }}/>   
                        </div>
                    </div>
                    <div className='ADinput'>
                        <Select
                            label="Cost"
                            value={cost}
                            onChange={updateCost}
                            variant='outlined'
                            style={{ width: '100%' }}
                        >
                            <MenuItem value={'low'}>$ (Low Cost)</MenuItem>
                            <MenuItem value={'mid'}>$$ (Mid Cost)</MenuItem>
                            <MenuItem value={'high'}>$$$ (High Cost)</MenuItem>
                        </Select>
                    </div>
                    <div className='ADinput ADsplit'>
                        <TextField
                            id="time"
                            label="Open Time"
                            type="time"
                            value={formalTimeConvert(stime)}
                            onChange={updatestime}
                            className='ADWide'
                            InputLabelProps={{
                                shrink: true,
                            }}
                            inputProps={{
                                step: 300, // 5 min
                            }}
                        />
                        <div style={{width: '2%'}}></div>
                        <TextField
                            id="time"
                            label="Close Time"
                            type="time"
                            value={formalTimeConvert(etime)}
                            onChange={updateetime}
                            className='ADWide'
                            InputLabelProps={{
                                shrink: true,
                            }}
                            inputProps={{
                                step: 300, // 5 min
                            }}
                        />
                    </div>
                    <div className='ADinput'>
                        <CategorySelect cat={cat} setCats={setCat}/>
                    </div>
                    <div className='ADinput'>
                        <TextField size='small' label="Link" variant="outlined" value={link} onChange={updateLink} style={{ width: '100%' }}/>   
                    </div>
                    <div className='ADinput'>
                        <TextField size='small' label="Description" variant="outlined" multiline rows={4} value={desc} onChange={updateDesc} style={{ width: '100%' }}/>   
                    </div>
                    <div className='ADinput'>
                        <Button onClick={handleAdd} variant="contained" color="primary" style={{ width: '100%' }}>Update Entry</Button>
                    </div>
                </div>
            </Dialog>
        </div>
    )
}