import React, { useEffect, useState } from 'react';
// import { useHistory } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import logo2 from '../../statics/Component 3 – 1@2x.png';
import line from '../../statics/Line 6.png';
import './NavBar.css';
import {Link} from 'react-router-dom';

import Login from "../Login/Login.js";

function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return {
    width,
    height
  };
}

function Entry(props){
  var link = "/";
  if(props.name === "Resources Search"){
    link = "/search";
  }
  if(props.name === "About"){
    link = "/about";
  }
  if(props.name === "Programs"){
    link = "/programs";
  }
  if(props.name === "Video Resources"){
    link = "/video";
  }
  if(props.name === "Contact"){
    link = "/contact";
  }

  if(props.name === props.select){
    return(
      <div>
        <Link className="biglink" to={link} onClick={() => props.act(props.name)}>{props.name}</Link>
        <hr className="bar"/>
      </div>
      )
  }
  else{
    return(
      <div>
        <Link className="biglink" to={link} onClick={() => props.act(props.name)}>{props.name}</Link>
      </div>
      )
  }
}

export default function NavBar(props) {
  const [item, selectItem] = useState("Home");
  const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

  useEffect(() => {
    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(()=>{
    let url = window.location.pathname

    if (url == "/"){
      selectItem("Home")
    }
    if (url == "/search"){
      selectItem("Home")
    }
    if (url == "/about"){
      selectItem("About")
    }
    if (url == "/contact"){
      selectItem("Contact")
    }
    if (url == "/video"){
      selectItem("Video Resources")
    }
    if (url == "/programs"){
      selectItem("Programs")
    }

  },[])

  const [open, setOpen] = useState(false)
  const login = () => {
    setOpen(true)
  }
  const closeLogin = () => setOpen(false)

  const logout = () => {
    localStorage.removeItem('jwt')
    window.location.reload();
  }

  return(
      <div className='NavContainer'>
        <div className="logo">
          <div /> <div />
          <div className="logoimg"><img src={logo2} alt="Fitnut" /></div>
          <div/>
          <div style={{marginRight:'15px'}}>
            {
              (localStorage.getItem('jwt')) ? (
                <>
                  <Button variant="contained" color="primary" onClick={logout} style={{ width: '100%', background: "#4E828B", marginRight: '15px' }}>
                    Logout
                  </Button>
                </>
              ) : (
                <>
                  <Button variant="contained" color="primary" onClick={login} style={{ width: '100%', background: "#4E828B", marginRight: '15px' }}>
                    Login
                  </Button>
                </>
              )
            }
            <Login 
              open={open}
              onClose={closeLogin}
            />
          </div>
        </div>
        <div className="lower-bar">
          <div className="options">
            <Entry name={(windowDimensions.width > 850) ? 'Resources Search' : 'Resources'} select={item} act={(i)=>selectItem(i)}/>
            <div><img src={line} alt="line"/></div>
            <Entry name="Home" select={item} act={(i)=>selectItem(i)}/>
            <div><img src={line} alt="line"/></div>
            <Entry name="About" select={item} act={(i)=>selectItem(i)}/>
            <div><img src={line} alt="line"/></div>
            <Entry name="Programs" select={item} act={(i)=>selectItem(i)}/>
            <div><img src={line} alt="line"/></div>
            <Entry name={(windowDimensions.width > 850) ? 'Video Resources' : 'Videos'} select={item} act={(i)=>selectItem(i)}/>
            <div><img src={line} alt="line"/></div>
            <Entry name="Contact" select={item} act={(i)=>selectItem(i)}/>
          </div>
        </div>
      </div>
  )
} 

