import React, { useEffect, useState } from 'react';
import Radio from '@material-ui/core/Radio';

import './FilterDrop.css';

export default function FilterDrop(props) {
  let { toggle, costs, setCosts, cats, setCats} = props

  const [cost, setCost] = useState(costs)
  const [low, setLow] = useState(false)
  const togLow = () => setLow(!low)
  const [mid, setMid] = useState(false)
  const togMid = () => setMid(!mid)
  const [high, setHigh] = useState(false) 
  const togHigh = () => setHigh(!high)

  useEffect(() =>{
    let nCost = {
      low,
      mid,
      high
    }
    setCost(nCost)
    setCosts(nCost)
  },[low, high, mid])

  const [cat, setCat] = useState(cats)
  const [groc, setGroc] = useState(false)
  const togGroc = () => setGroc(!groc)
  const [out, setOut] = useState(false)
  const togOut = () => setOut(!out)
  const [rest, setRest] = useState(false)
  const togRest = () => setRest(!rest)
  const [clas, setClas] = useState(false)
  const togClas = () => setClas(!clas)
  useEffect(()=> {
    let store = {
      groc,
      out,
      rest,
      clas,
      any: false
    }
    if (groc == false && out == false && rest== false && clas == false){
      store.any = true
    }
    setCat(store)
    setCats(store)
  },[groc, out, rest, clas])

  return (
    <div className={`filterContainer ${toggle ? '' : 'closeFil' }`}>
      <div>
        <div className='filminititle'>Cost</div>
        <div className='costBar'>
          <div className={`dollarhov ${low ? 'hardenCost' : ''}`} name='low' onClick={togLow}>$</div>
          <span className={`dollarhov ${mid ? 'hardenCost' : ''}`} name='mid' onClick={togMid}>$$</span>
          <span className={`dollarhov ${high ? 'hardenCost' : ''}`} name='high' onClick={togHigh}>$$$</span>
        </div>
        <div className='filminititle'>Resource type</div>
        <div className='catBox'>
          <span style={{ marginLeft: '3px'}}>No selections will show all</span>
          <div className='divBar'/>
          <div className='catSplit'>
            <div className='catCol catleft'>
              <div className='entriesCat'>
                <Radio checked={groc} onClick={togGroc} color='primary' size='small'/>
                <span>Grocery</span>
              </div>
              <div className='entriesCat'>
                <Radio checked={out} onClick={togOut} color='primary' size='small'/>
                <span>Outdoor</span>
              </div>
            </div>
            <div className='catCol catright'>
              <div className='entriesCat'>
                <Radio checked={rest} onClick={togRest} color='primary' size='small'/>
                <span>Restaurant</span>
              </div>
              <div className='entriesCat'>
                <Radio checked={clas} onClick={togClas} color='primary' size='small'/>
                <span>Class</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
} 