import React, { useEffect, useState } from 'react';
import Radio from '@material-ui/core/Radio';

import './CategoryVideos.css';

export default function CategoryVideos(props) {
    let { cat, setCats } = props

    const [catz, setCatz] = useState(cat)
    const [E, setE] = useState(false)
    const togE = () => setE(!E)
    const [M, setM] = useState(false)
    const togM = () => setM(!M)

    useEffect(()=>{
        for (let i = 0; i != cat.length; i++){
            if (cat[i] === 'M'){
                setM(true)
            }
            if (cat[i] === 'E'){
                setE(true)
            }
        }
    },[cat])


    useEffect(() =>{
        let output = []
        if (M === true){
            output.push('M')
        }
        if (E === true){
            output.push('E')
        }
        setCatz(output)
        setCats(output)
    },[M, E])

    return (
        <div>
            <span style={{textDecoration:'underline'}}>Select Categories</span>
            <div className='catSplit paddingbottom__CS'>
                <div className='catCol catleft'>
                    <div className='entriesCat'>
                        <Radio checked={M} onClick={togM} color='primary' size='small'/>
                        <span>Music</span>
                    </div>
                    <div className='entriesCat'>
                        <Radio checked={E} onClick={togE} color='primary' size='small'/>
                        <span>Excercise</span>
                    </div>
                </div>
            </div>
        </div>
    )
} 