import React from 'react';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownwardRounded';
import Profile from '../../statics/defaultpfp.jpg';
import Grid from '@material-ui/core/Grid';
import './AboutPage.css';

import Sameer from '../../statics/Sameer.jpeg';
import Mindy from '../../statics/Mindy.jpeg';
import Arjun from '../../statics/Arjun.jpeg';
import Andy from '../../statics/Andy.jpeg';
import Jessica from '../../statics/Jessica.jpeg';

function Heading() {
    return (
        <div className="header">
            <h2>
                About Us
            </h2>
            <h4>
                <hr />
                All Work. And All Play.
            </h4>
        </div>
    );
}

function Content() {
    return (
        <div className="white-background about-us">
            <div>
                SCOPE Fitness and Nutrition is a
                health education program teaching
                fitness and nutrition to 3rd, 4th, and
                5th graders in Los Angeles. The
                program’s main goal is to improve
                health outcomes by educating
                children on how to stay healthy by
                eating nutritious foods and staying
                physically active. In addition, we are
                dedicated to raising awareness on the
                social determinants of health and
                encouraging our students to become
                active members in their communities.
                At the same time, Fitness and
                Nutrition teachers gain valuable
                leadership skills and knowledge about
                the community, its health barriers,
                and burdens. Fitness and Nutrition
                teachers will gain teaching skills,
                learn about childhood health, fitness
                and nutrition, and most importantly,
                build a positive and invaluable relationship
                with the community.
            </div>
        </div>
    );
}


function Mission() {
    return (
        <div className="white-background mission-summary">
            <h2>
                Our Mission
            </h2>
            <hr />
            <div>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure
                dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident,
            </div>
        </div>
    );
}

function MeetOurTeam() {

    return (
        <div className="white-background meet-team">
            <h3>
                Meet Our Team
            </h3>
            <ArrowDownwardIcon fontSize="large"/>
        </div>
    )
}

function Recruit() {
    return (
        <div className="white-background recruitment">
            <h2>
                Fall Recruitment 2020-2021
            </h2>
            <br/>
            <a href="https://bit.ly/FitNutApp20202021" className="apply_link"> </a>
        </div>
    );
}

function Member(props) {
    return (
        <div className="profile">
            <img src={props.pic} className="image_about" alt="" />
            <h5>
                {props.name}
            </h5>
            <p style={{padding: '-20px 0'}}>
                {props.position}
            </p>
            <p style={{fontSize: '18px', padding: '0 10px'}}>
                {props.description}
            </p>
        </div>
    )
}

function MemberDisplay1(props) {
    return (
        <div className="member-display">
            <h3>
                {/* {props.title} */}
            </h3>
            {/* can change this to a flex box if needed, but grid might be easier for mapping */}
            <Grid container
                spacing={3}
                direction="row"
                justify="space-evenly"
                alignItems="center"
            >
                <Grid item xs={6} sm={3}>
                    <Member
                        name="Sameer Kejriwal"
                        position="Member"
                        description="Hey everyone! I have been part of FitNut for two years and it has been my favorite college experience. This club allows me to channel my inner-child enthusiasm and my interest in athletics. Volunteering with LA elementary schools and the Boys and Girls Club exposes me to barriers that students face when trying to maintain healthy lifestyles. I hope to continue addressing these social determinants of health through our weekly lessons and for the rest of my life."
                        pic={Sameer}
                    />
                </Grid>
                <Grid item xs={6} sm={3}>
                    <Member
                        name="Mindy Kim"
                        position="Member"
                        description="Hi everyone! I’m Mindy and this is my 3rd year in FitNut! FitNut has undoubtedly been one of the most influential experiences during my time at UCLA. Site visit day has become the highlight of my week, and being able to teach kids about the importance of physical and mental health has been an engaging and invaluable experience. FitNut has spurred my personal interest in maintaining a healthy and active lifestyle and exposed me to the need for more effective and digestible health education, especially in underserved and disadvantaged communities."
                        pic={Mindy}
                    />
                </Grid>
                <Grid item xs={6} sm={3}>
                    <Member
                        name="Arjun Sharma"
                        position="Member"
                        description="Whaddup people, my name is Arjun and this is my second year getting the pleasure to work with FitNut! I’ve always loved leading an active, balanced lifestyle and teaching, so this club was a perfect fit for me. I’ve had some of my favorite college experiences getting to work with our elementary schoolers and amazing club membership. I hope to carry the health values and teaching skills I’ve learned with this club into my future career and continue to help underserved communities wherever I find myself."
                        pic={Arjun}
                    />
                </Grid>
            </Grid>
        </div>
    );
}

function MemberDisplay2(props) {
    return (
        <div className="member-display">
            <h3>
                {/* {props.title} */}
            </h3>
            {/* can change this to a flex box if needed, but grid might be easier for mapping */}
            <Grid container
                spacing={3}
                direction="row"
                justify="space-evenly"
                alignItems="center"
            >
                <Grid item xs={6} sm={3}>
                    <Member
                        name="Andy Ho"
                        position="Member"
                        description="Hey what’s up everybody, I’m Andy and I’m a second year FitNut member. I’ve been all about helping kids as I grew up with two little brothers, and this org really gives it all back to the Los Angeles community around UCLA. I’ve learned a lot not only from my fellow members, but the teachers and students I work with as well. FitNut’s been everything I wanted and I hope to continue making the kids happy."
                        pic={Andy}
                    />
                </Grid>
                <Grid item xs={6} sm={3}>
                    <Member
                        name="Jessica McLaughlin"
                        position="Member"
                        description="Hey y’all, I’m Jess and this is my 2nd year being involved in FitNut! FitNut has been a big part of my college experience and has made me feel connected to the great LA community. Some of my favorite things about being in FitNut are being a part of a tight-knit group of students who want to make a difference and working with eager Los Angeles youth. I have learned invaluable lessons from my students and look forward to continuing to work side-by-side with our LA community."
                        pic={Jessica}
                    />
                </Grid>
            </Grid>
        </div>
    );
}


export default function AboutPage(props) {
    return (
        <div className="main-container">
            <div className="grid-container">
                <div className="left">
                    <Heading />
                    <Content />
                </div>
                <div className="right">
                    <Mission />
                    <MeetOurTeam />
                    <Recruit />
                </div>
            </div>
            <div className="footer">
                <div className="our-team">
                    Our Team
                </div>
                <MemberDisplay1/>
                <MemberDisplay2/>
            </div>
        </div>
    )
};