import React,{ useEffect, useState} from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import './App.css';

// Component Imports
import {
  MainPage,
  SearchPage, 
  AboutPage,
  TemporaryPage,
  VidPage
} from './Pages'
import {
  NavBar
} from './components'

export default function App() {

  return (
      <div>
        <BrowserRouter> 
        <div style={{position: "relative",zIndex:2}}>
          <NavBar/>
        </div>
          <div style={{position: "relative",zIndex:1}}>
            <Switch>
              <Route exact path='/search' component={ SearchPage }/>
              <Route exact path='/' component={ MainPage }/> 
              <Route exact path='/about' component={ AboutPage }/>
              <Route exact path='/programs' component={ TemporaryPage }/>
              <Route exact path='/video' component={ VidPage }/>
              <Route exact path='/contact' component={ TemporaryPage }/>
            </Switch>
          </div>
        </BrowserRouter>
      </div>
    );
}