import React, { useEffect, useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import Axios from 'axios';

import './NewVideo.css'

import { CategoryVideos } from '../../components';

export default function NewVideo(props) {
    let { onClose, open, flip, setFlip, full, setFull } = props
  
    const [id, setId] = useState('')
    const updateId = (event) => setId(event.target.value)
    const [diffc, setDiffc] = useState('1')
    const updateDiff = (event) => setDiffc(event.target.value)
    const [catt, setCatt] = useState([])

    const [pass, setPass] = useState(false)
    const updatePass = () => setPass(!pass)

    const closeUp = () => {
        setId('')
        setDiffc('1')
        setPass(false)
        setCatt([])
        onClose()
    }

    const handleAdd = async () =>{
        if (id.length == 0){
            return window.alert('Enter valid Video ID.')
        }
        updatePass()

        let newdata = {
            videoID: id,
            diff: diffc,
            category: catt
        }

        var request = null
        let error = false
        try{
            request = await Axios.post('/video/addVideo', newdata)
        } catch(err){
            error = true
            setPass(false)
            window.alert('Ensure valid Video ID.')
        }

        if (!error){
            let info = full
            info = info.splice(0,0,request.data)
            setFull(info)
            closeUp()
        }
    } 
    
    return(
        <div>
            <Dialog onClose={closeUp} open={open}>
                <div className='ADbox'>
                    <div className='ADinput ADtext'>
                        <span>Add Video Entry</span>
                    </div>
                    <div className='ADinput'>
                        <TextField size='small' label="Youtube Video ID" variant="outlined" value={id} onChange={updateId} style={{ width: '100%' }}/>   
                    </div>
                    <div className='ADinput'>
                        <Select
                            label="Difficulty"
                            value={diffc}
                            onChange={updateDiff}
                            variant='outlined'
                            style={{ width: '100%' }}
                        >
                            <MenuItem value={'1'}>1 (Easy)</MenuItem>
                            <MenuItem value={'2'}>2 (Medium)</MenuItem>
                            <MenuItem value={'3'}>3 (Tough)</MenuItem>
                        </Select>
                    </div>
                    <div className='ADinput'>
                        <CategoryVideos cat={catt} setCats={setCatt}/>
                    </div>
                    <div className='ADinput'>
                        <Button onClick={handleAdd} disabled={pass} variant="contained" color="primary" style={{ width: '100%' }}>Add Video Entry</Button>
                    </div>
                </div>
            </Dialog>
        </div>
    )
}