import React, { useEffect, useState } from 'react';
import Axios from 'axios';
import Dialog from '@material-ui/core/Dialog';

import './DeleteDialog.css';

export default function DeleteDialog(props) {
    let { onClose, open, flip, setFlip, data, index, setData, full } = props

    const deleteHandle = async () => {
        var request = null
        try{
            request = await Axios.post(`/api/removeStore/${data.id}`)
        } catch(err){
            console.log(err)
        }

        let info = full
        info.splice(index, 1)
        setData(info)
        setFlip(!flip)
        onClose()
    }

    return(
        <div>
            <Dialog open={open} onClose={onClose}>
                <div className='DDbox'>
                    <div>
                        <span>Are you sure you want to delete entry: {data.name}?</span>
                    </div>
                    <div className='DDbuttonbox'>
                        <button onClick={deleteHandle} className='DDbutton DDleft'>Delete</button>
                        <button onClick={onClose} className='DDbutton DDright'>Cancel</button>
                    </div>
                </div>
            </Dialog>
        </div>
    )
}