import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Axios from 'axios';

import './Login.css'

export default function Login(props) {
    let { onClose, open } = props

    const [user, setuser] = React.useState("");
    const [password, setpassword] = React.useState("");
    const [message, setmsg] = React.useState("");

    const closer = () =>{
        setuser("");
        setpassword("");
        setmsg("");
        onClose();
    }

    const handleClose = async (x) => {
        if(x){
            let u = user;
            let p = password;
            //Empty strings -> Reject straight away
            if((!u || !u.trim()) || (!p || !p.trim())){
                console.log('1')
                setmsg("Error: Please enter valid credentials")
            }
            //Try authenticating
            else{
                let request = {username: user, password: password};
                //Success
                try{
                    let response = await Axios.post('/auth/login', request);
                    let token = response.data.token;
                    console.log(token !== 'PxsaNpwa5H6tQ2vg1fxu')
                    if (token !== 'PxsaNpwa5H6tQ2vg1fxu'){
                        console.log(token)
                        setmsg("Error: Please enter valid credentials");
                        setuser("");
                        setpassword("");
                        return;
                    }
                    setmsg("");
                    localStorage.setItem('jwt', true)
                    onClose();
                    window.location.reload();
                }
                //Failure
                catch(err){
                    console.log('2')
                    setmsg("Error: Please enter valid credentials");
                }
            }
            setuser("");
            setpassword("");
        } 
    };

    return (
        <div className='loginDialog_log'>
            <Dialog open={open} onClose={closer} aria-labelledby="form-dialog-title">
                <div className='container_log'>
                    <span className='title_log'>Login</span>
                    <br></br>
                    {
                        (message.length == 0) ? null : (
                            <>
                                <span className='error_log' >{message}</span>
                            </>
                        )
                    }
                    <DialogContent>
                        <div className='ADinput'>
                            <TextField
                                variant="outlined"
                                margin="dense"
                                id="name"
                                label="Username"
                                type="email"
                                size="medium"
                                style={{ width: '100%' }}
                                value={user}
                                onChange={(event)=>setuser(event.target.value)}
                            />
                        </div>
                        <div className='ADinput'>
                            <TextField
                                variant="outlined"
                                margin="dense"
                                id="name"
                                label="Password"
                                type="passsord"
                                size="medium"
                                style={{ width: '100%' }}
                                value={password}
                                onChange={(event)=>setpassword(event.target.value)}
                            />
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={closer} variant="contained" color="primary" style={{ width: '90%'}}>
                            Cancel
                        </Button>
                        <Button onClick={() => handleClose(true)} color="secondary" variant="contained" style={{ width: '90%'}}>
                            Submit
                        </Button>
                    </DialogActions>
                </div>
            </Dialog>
        </div>
    )
}