import './MainPage.css';
import Stretching from "../../statics/stretching.jpg"
import Meditation from "../../statics/Yoga.jpg"
import { Thumbnail } from '../../components/index.js'


function Volunteer(props){
    return(
    <div className="white-banner volunteer_sites">
        <br />
        <br />
        <div className="heading_volunteer"> Current Volunteer Sites</div>
        <hr className="long"/>
        BRADDOCK ELEMENTARY | EDISON LANGUAGE ACADEMY | NORA STERRY ELEMENTARY | PALMS ELEMENTARY | 
        STONER ELEMENTARY
    </div>
    );
}

function Recruit(props){
    return(
    <div className="white-banner recruitment"> 
        <div className="r">
            <br />
            Fall Recruitment 2020-2021
        </div>
        <br/>
        <a href="https://bit.ly/FitNutApp20202021" className="apply_link"> </a>
    </div>
    );
}

function Heading(){
    return(
    <div className="heading_M"> 
        <div className="heading_top"> 
            Fitness and Nutrition
        </div>
        <div className="heading_bottom">
            <hr/>
            All Work. And All Play.
        </div>
    </div>
    );
}
function Content(){
    return(
    <div className="white-banner about-SCOPE"> 
        <br/>
        SCOPE Fitness and Nutrition is a health education program teaching fitness and nutrition to 
        3rd, 4th, and 5th graders in Los Angeles. The program’s main goal is to improve health outcomes
        by educating children on how to stay healthy by eating nutritious foods and staying physically 
        active. In addition, we are dedicated to raising awareness on the social determinants of health and
        encouraging our students to become active members in their communities. At the same time, Fitness and 
        Nutrition teachers gain valuable leadership skills and knowledge about the community, its health barriers, and burdens. 
        Fitness and Nutrition teachers will gain teaching skills, learn about childhood health, fitness and nutrition, and most 
        importantly, build a positive and invaluable relationship with the community.
    </div>
    );
}

export default function MainPage(props) {
    return(
        <div className= "main_M">
            <div className="left_M">
                <Heading />
                <Content />
            </div>
            <div className="right_M">
                <Volunteer type="test_M"/>
                <Recruit type="test_M"/>
                <div>
                    <div className="featured_videos"> Featured Videos </div>
                    <br /> <br />
                    <div className="videos">
                        <Thumbnail type="5-Minute Meditation" difficulty="1" id={'inpok4MKVLM'} time="5" image={'https://i.ytimg.com/vi/inpok4MKVLM/hqdefault.jpg'}/>
                        <Thumbnail type="Create a Healthy Plate" difficulty="1" id={'Gmh_xMMJ2Pw'}time="2" image={'https://i.ytimg.com/vi/Gmh_xMMJ2Pw/hqdefault.jpg'}/>
                    </div>
                    <br />
                    <br />
                </div>
            </div>
        </div>
    )
}