import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

import './Thumbnail.css';

export default function Thumbnail(props){

    const redirect = () => {
        let url = `https://www.youtube.com/watch?v=${props.id}`
        window.open(url, '_blank');
    }

return(
    <div className="vidinfo" onClick={redirect}>
        <div className='title_thumb'> {props.type}</div>
        <div className="smallinfo">
            Difficulty: {props.difficulty} <br />
            Time: {props.time} min
        </div>
        <div className='imgSpread'>
            <img className='imgSpread' src={props.image} alt= {props.alt}/> 
        </div>
    </div>
);
}

