import React, { useEffect, useState } from 'react';
import SearchIcon from '@material-ui/icons/Search';
import MenuOpenIcon from '@material-ui/icons/MenuOpen';
import { FilterDrop, Map, SearchCard, AddDialog } from '../../components';
import Axios from 'axios';

import './SearchPage.css';

import tryBar from '../../statics/resourcesearch.png';

export default function SearchPage(props) {
  const [search, setSearch] = useState('')
  const updateSearch = event => setSearch(event.target.value)

  const [load, setLoad] = useState(false)
  const [show, setShow] = useState(false)

  const [filter, toggleFilter] = useState(false)
  const setFilter = () => toggleFilter(!filter)

  const [logged, setLogged] = useState(localStorage.getItem('jwt'))

  const [costs, setCosts] = useState({
    low: false,
    mid: false,
    high: false
  }) 

  const [cats, setCats] = useState({
    groc: false,
    out: false,
    rest: false,
    clas: false,
    any: true
  })

  const handleKeyPress = (event) => {
    if(event.key === 'Enter'){
      searchData()
    }
  }

  // States for results
  const [data, setData] = useState([])
  const [names, setNames] = useState([])
  const [address, setAddress] = useState([])
  const [zip, setZip] = useState([])
  const [length, setLength] = useState(0)
  const [flip, setFlip] = useState(false)

  // Lat and Long States
  const [lat, setLat] = useState([])
  const [long, setLong] = useState([])

  const [index, setIndex] = useState(0)

  // Map States
  const defaultZoom = 14
  // Hover functions --> This works by setting an array of falses, if any of the links are detected to hover; then we will turn the 
  // corresponding index true, and this should be fed into the componenet so that the effects can be put in effect. 
  const [hover, setHover] = useState(-1)

  // Data compilation for API
  const findResults = async (event) => {

    if (search === ''){
      return alert("Please enter something into search bar.")
    } 
    setLoad(true)
    setShow(false)

    let costList = []
    if (costs.low === true){
      costList.push("low")
    }
    if (costs.mid === true) {
      costList.push("mid")
    }
    if (costs.high === true){
      costList.push("high")
    }

    let catList = []
    if (cats.groc == true){
      catList.push('G')
    }
    if (cats.out == true){
      catList.push('O')
    }
    if (cats.rest == true){
      catList.push('R')
    }
    if (cats.clas == true){
      catList.push('C')
    }
    if (cats.any == true){
      catList = ['G','O','R','C']
    }
    

    // The Request we send!
    let request = {
      search,
      filters: {
        cost: costList,
        category: catList
      }
    }

    var answer = null
    try{
      answer = await Axios.post('/api/getStores', request)
    } catch(err){
      console.log(err)
    } 

    let myData = answer.data

    setLength(myData.length)

    // data proccessing
    let nameList = []
    let addressList = []
    let zipList = []
    let dataList = []

    for (let i = 0; i !== myData.length; i++){
      let res = myData[i]
      nameList.push(res.name)
      addressList.push(res.address)
      zipList.push(res.zip)

      let temp = {
        name: myData[i].name,
        address: myData[i].address,
        zip: myData[i].zip,
        city: myData[i].city,
        cost: myData[i].cost,
        link: myData[i].link,
        desc: myData[i].desc,
        hours: myData[i].hours,
        category: myData[i].category,
        id: myData[i]._id
      }
     
      dataList.push(temp)
    }

    setData(dataList)

    setNames(nameList)
    setAddress(addressList)
    setZip(zipList)

    populateCoords(addressList, zipList)
  }

  const populateCoords = async (addressList, zipList) => {
    let latList = []
    let longList = []
    // Loop for all the entries
    for (let i = 0; i !== addressList.length; i++){
        // set up URL with correct parameters
        const {REACT_APP_GOOGLEAPI} = process.env
        let url = `https://maps.googleapis.com/maps/api/geocode/json?address=${addressList[i].split(' ').join('+')},${zipList[i]}&key=${REACT_APP_GOOGLEAPI}`

        // Api request to google
        var data
        try{
            data = await Axios.get(url)
        } catch (err){
            console.log(err.message)
        }

        // setting the temporary lists
        let location = data.data.results[0].geometry.location

        latList.push(location.lat)
        longList.push(location.lng)
    }
    
    // setting States
    setLat(latList)
    setLong(longList)

    setLoad(false)
    setShow(true)
  } 

  const [open1, setOpen1] = useState(false)
  const addEntry = () => {

    setOpen1(true)
  }
  const addEntryClose = () => setOpen1(false)

  const searchData = () => {
    findResults()
  }
  
  return (
    <div className='searchContainer'>
      <div className='barBox'>
        <div className='seudoBar'>
          <input type='text' placeholder='Search resources' className='searchBar' onKeyPress={handleKeyPress} onChange={updateSearch} value={search}></input>
          <div className='searchIcon button' onClick={searchData}>
            <SearchIcon />
          </div>
        </div>
        <div className={`filterButton button ${filter ? 'moveFilter' : ''}`} onClick={setFilter}>
          <div className={`menuIcon ${filter ? 'reflect' : ''}`}>
            <MenuOpenIcon/>
          </div>
          <span>Filters</span>
        </div>
      </div>
      <div className='fil'>
        <FilterDrop toggle={filter} costs={costs} setCosts={setCosts} cats={cats} setCats={setCats}/>
      </div>
      <div className='bottom'>
        { show ? (
          <>
            <div className='leftSide'>
              <Map lat={lat} long={long} hover={hover} defaultZoom={14} names={names} />
            </div>
            <div className='rightSide'>
              <div>
                <div style={{display:'flex'}}>
                  <h3 className="subText">Search Results -- Top {length} Shown</h3>
                  { logged ? <div className='addB' onClick={addEntry}>Add Resource</div> : null }
                </div>
                  {
                    data.map((object, index) => <SearchCard 
                      setData={setData} 
                      full={data} 
                      logged={logged} 
                      flip={flip} 
                      setFlip={setFlip} 
                      setHover={setHover} 
                      data={object} 
                      index={index} 
                      key={index}/>)
                  }
              </div>
            </div>
            <AddDialog
              flip={flip}
              setFlip={setFlip}
              open={open1} 
              onClose={addEntryClose}
            />
          </>
        ) : load ? (
          <>
            <div className='midLoad'>
              Loading results...
            </div>
          </>
        ) : (
          <>
            <div className='searchintro'>
              <img className='support' src={tryBar}></img>
            </div>
          </>
        ) }
      </div>
    </div>
  )
} 

