import React, { useEffect, useState } from 'react';
import Radio from '@material-ui/core/Radio';

import './CategorySelect.css';

export default function CategorySelect(props) {
    let { cat, setCats } = props

    const [catz, setCatz] = useState(cat)
    const [G, setG] = useState(false)
    const togG = () => setG(!G)
    const [O, setO] = useState(false)
    const togO = () => setO(!O)
    const [R, setR] = useState(false)
    const togR = () => setR(!R)
    const [C, setC] = useState(false)
    const togC = () => setC(!C)

    useEffect(()=>{
        for (let i = 0; i != cat.length; i++){
            if (cat[i] === 'G'){
                setG(true)
            }
            if (cat[i] === 'R'){
                setR(true)
            }
            if (cat[i] === 'O'){
                setO(true)
            }
            if (cat[i] === 'C'){
                setC(true)
            }  
        }
    },[cat])


    useEffect(() =>{
        let output = []
        if (G === true){
            output.push('G')
        }
        if (O === true){
            output.push('O')
        }
        if (R === true){
            output.push('R')
        }
        if (C === true){
            output.push('C')
        }
        setCatz(output)
        setCats(output)
    },[G, O, R, C])

    return (
        <div>
            <span style={{textDecoration:'underline'}}>Select Categories</span>
            <div className='catSplit paddingbottom__CS'>
                <div className='catCol catleft'>
                    <div className='entriesCat'>
                        <Radio checked={G} onClick={togG} color='primary' size='small'/>
                        <span>Grocery</span>
                    </div>
                    <div className='entriesCat'>
                        <Radio checked={O} onClick={togO} color='primary' size='small'/>
                        <span>Outdoor</span>
                    </div>
                </div>
                <div className='catCol catright'>
                    <div className='entriesCat'>
                        <Radio checked={R} onClick={togR} color='primary' size='small'/>
                        <span>Restaurant</span>
                    </div>
                    <div className='entriesCat'>
                        <Radio checked={C} onClick={togC} color='primary' size='small'/>
                        <span>Class</span>
                    </div>
                </div>
            </div>
        </div>
    )
} 