import React, { useEffect, useState } from 'react';
import GoogleMapReact from 'google-map-react';
import { Marker } from '../../components';

import './Map.css';

export default function Map(props) {
  const {long, lat, defaultZoom, hover, names } = props
  
  return(
    <div style={{ height: '100%', width: '100%' }}>
        <GoogleMapReact
            bootstrapURLKeys={{ key: 'AIzaSyAAWZusRNTdK2L4qwPjEu5ytwGwcn9rYA8' }}
            defaultCenter={{
                lat: lat[0],
                lng: long[0]
            }}
            defaultZoom={defaultZoom}
        >
            {
                names.map((place,index)=>{
                    return <Marker
                        lat={lat[index]}
                        lng={long[index]}
                        text={place}
                        hover={hover}
                        index={index}
                        key={index}
                    />
                })
            }
        </GoogleMapReact>
    </div>
  )
} 

